import React from 'react';
import style from "./order-details.module.css";
import orderAccepted from '../../images/order_accpeted.png';
import { useSelector } from 'react-redux';

const OrderDetails = () => {
  const number = useSelector((store) => store.order.number);

	return (
		<div className={`${style.container} pl-25 pr-25`}>
			<h3 className={`${style.title} text text_type_digits-large pt-15 pb-8`}>{number}</h3>
			<p className={`${style.text} text text_type_main-medium pb-15`}> </p>
			<img className={`${style.icon} pb-15`} src={orderAccepted} alt={orderAccepted} />
			<p className={`${style.text} text text_type_main-default pb-2`}>   </p>
			<p className={`${style.text} text text_type_main-default text_color_inactive pb-30`}>    </p>
		</div>
	)
}

export default OrderDetails;